const app = getApp();
import MescrollMixin from '../componets/mescroll-uni/mescroll-mixins.js';
export default {
	mixins: [MescrollMixin], // 使用mixin
	data() {
		return {
			subject: '科目',
			Inv: '0',
			type: '',
			isShow: false,
			nav: [],
			list: [],
			classify_id: '-1',
			downOption: {
				auto: true //是否在初始化后,自动执行downCallback; 默认true
			},
			upOption: {
				page: {
					num: 0, // 当前页码,默认0,回调之前会加1,即callback(page)会从1开始
					size: 20 // 每页数据的数量,默认10
				},
				noMoreSize: 5, // 配置列表的总数量要大于等于5条才显示'-- END --'的提示
				empty: {
					tip: '暂无相关数据'
				},
				textNoMore: '没有更多数据了'
			}
		};
	},
	onShow() {
		this.mescroll.resetUpScroll();
	},
	onLoad(option) {
		this.type = option.type;
	},
	methods: {
		// 选项卡
		screenShow(e) {
			this.Inv = e;
			if ((this.isShow = true)) {
				this.isShow = false;
			} else {
				this.isShow = true;
			}
			if (e != '0') {
				this.isShow = true;
			}
			if(e == '0'){
				this.classify_id = '-1'
				this.mescroll.resetUpScroll();
			}else{
				this.getNav()
			}
			
		},
		getShow() {
			this.isShow = false;
		},
		// 筛选
		navItem(e,id) {
			this.subject = e;
			this.isShow = false;
			this.classify_id = id
			this.list = []
			this.mescroll.resetUpScroll();
		},
		getNav(){
			uni.request({
				url: `${this.$helper.requestUrl}class/video_classify`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					res = this.$helper.null2str(res.data);
					if (res.status_code == 'ok') {
						this.nav = res.data
					} else {

					}
				}
			});
		},
		/*下拉刷新的回调 */
		downCallback() {
			this.mescroll.resetUpScroll();
		},
		upCallback(page) {
			this.getList(page);
		},
		getList(page) {
			uni.showLoading({
				title: '加载中...',
			})
			uni.request({
				url: `${this.$helper.requestUrl}user/myExchangeCode`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data: {
					classify_id: this.classify_id,
					page_size: page.size,
					page: page.num
				},
				success: res => {
					uni.hideLoading()
					res = this.$helper.null2str(res.data);
					if (res.status_code == 'ok') {
						if (res.message == '暂无信息') {
							this.list = this.list.concat([]);
							this.mescroll.endByPage(0, 0);
						} else {
							if (page.num == 1) this.list = [];
							this.list = this.list.concat(res.data.data);
							this.mescroll.endByPage(res.data.data.length, res.data.total);
						}
					} else {
						
					}
				}
			});
		},
		// 批量赠送
		goMore() {
			uni.navigateTo({
				url: `/pages/saleConvert?type=批量`
			});
		},
		// 赠送
		goUse(e) {
			uni.navigateTo({
				url: `/pages/saleConvert?type=单个&obj=${JSON.stringify(e)}`
			});
		},
		// 购买
		goBuy() {
			let grade = app.globalData.userInfo.extension
			if(grade == 2 || grade == 3){
				uni.navigateTo({
					url: `/pages/contact`
				});
			}else if(grade == 1){
				uni.navigateTo({
					url: `/pages/buyConvert`
				});
			}
		}
	}
};
